const readline = require('readline');
const fs = require('fs').promises;
const { program } = require('commander');
require('colors');
program.option(
  '-f, --file [type]',
  'file for saving game results',
  'results.txt'
);
program.parse(process.argv);

const rl = readline.createInterface({
  input: process.stdin,
  output: process.stdout,
});

let count = 0;
const logFile = program.opts().file;
const mind = Math.floor(Math.random() * 10) + 1;

const isValid = value => {
  if (isNaN(value)) {
    console.log(' !'.red);
    return false;
  }
  if (value < 1 || value > 10) {
    console.log('     1  10'.red);
    return false;
  }
  return true;
};

const log = async data => {
  try {
    await fs.appendFile(logFile, `${data}\n`);
    console.log(`     ${logFile}`.green);
  } catch (err) {
    console.log(`    ${logFile}`.red);
  }
};

const game = () => {
  rl.question(
    '   1  10,   : '.yellow,
    value => {
      let a = +value;
      if (!isValid(a)) {
        game();
        return;
      }
      count += 1;
      if (a === mind) {
        console.log('     %d ()'.green, count);
        log(
          `${new Date().toLocaleDateString()}:      ${count} ()`
        ).finally(() => rl.close());
        return;
      }
      console.log('    '.red);
      game();
    }
  );
};

game();
